* {
  box-sizing: border-box;

}


html {
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans,
    Ubuntu, Droid Sans, Helvetica Neue, sans-serif;
}

body {
  // color: #1a1a1a;;
  // margin: 0 auto;
  // font-size: rem(14px);
  // // min-height: 100vh;
  padding: 0;
  margin: 0;
}


// main {
//   display: flex;
//   flex-grow: 1;
// }

img {
  width: 100%;
  object-fit: cover;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

p {
  margin: 0 auto;
}

button {
  all: unset;
  cursor: pointer;
}

input,
textarea {
  font-size: 14px;
  outline: none;
  border: none;
  resize: none;
  width: 100%;
}

a {
  text-decoration: none;
  color: inherit;
}

